@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500;700&display=swap);
*{font-family:'Montserrat', sans-serif}body{margin:0;font-family:'Montserrat', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.result__area{display:flex;min-height:100vh;justify-content:stretch;align-items:center;width:50%;background:#fff}.result__dashboard{padding:20px;width:100%}.result__dashboard h1{margin-bottom:30px;text-transform:uppercase;font-weight:700}.result__dashboard .result__left{display:flex;align-items:stretch}@media (max-width: 450px){.result__dashboard .result__left{display:block}}.result__dashboard .result__left .result__tiles{flex-shrink:0;flex-basis:33.33%;border-bottom:dashed 1px #ccc;padding:20px 0}.result__dashboard .result__left .result__tiles p{margin:0;padding:0;margin-bottom:10px}.result__dashboard .result__left .result__tiles h2{font-weight:700}.result__dashboard .result__right{padding:30px 0}@media (max-width: 450px){.result__area{width:100%}}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

